import { createApp } from 'vue'
import App from './App.vue'
const app = createApp(App);

// import router
import router from './router/index.js' 
app.use(router);

// import ant design vue and css
import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/antd.css';
app.use(Antd);

// import axios
import Axios from 'axios'
app.config.globalProperties.$axios = Axios;
app.config.globalProperties.$axios.defaults.baseURL = CONFIG.flaskUrl;

// tensorboard url
app.config.globalProperties.$tensorboard_url = CONFIG.tensorboardUrl;

app.mount('body');

